.header{
    height: 80px;
    display: flex;
    
    align-items: center;
    background-color: #2e3136;
    position: sticky;
    top: 0;
    z-index: 100;
}


.header_logo{
    width: 100px;
    height: 75px;
    object-fit: contain;
    margin: 0 2px;   
    margin-top: 0px;
    background-color: #2e3136;
}  


.header_text{
 display: flex;   
flex-direction: column;
margin-left: -1rem;
margin-right: 10px;
color: white;

}




.header_textLineOne {
    font-size: 12px;
    font-weight: lighter;
    background-color: #2e3136 !important;
}
    

.header_textLineTwo {
font-size: 20px;
font-weight: 50px;
background-color: #2e3136 !important;
}


.header_textRight{
    display: flex;
    flex-direction: column;
    color: white;
   max-width: 80px;
   position: absolute;
   right: 0; 
   margin-right: 45px;
  
}


.header_textRightBig{
    font-size: 20px;
font-weight: 60px;  
background-color: #2e3136 !important;
}


.header_textRightSmall {
    font-size: 15px;
    font-weight: lighter;
    background-color: #2e3136 !important;
}


.navContent{
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap:10px;
    list-style: none;
    text-align: center;
    width: 50vw;
    justify-content: center;
    margin-right: 15rem;
    background-color: #2e3136 !important; 
}

.navContent a{
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    position: relative;
    background-color: #2e3136 !important; 
}

.navContent a:hover{
  background-color: #4f545c;  
    border-radius: 4px;
    transition: all 0.5s ease-out;
    text-decoration: none;
}

.fa-bars{
    color: white;
    
}
.fa-times{
    color: white;
}

.menu-icon{
    display: none;
}



@media screen and (max-width: 960px){
.header{
    position: sticky;
}
.header_textRight{
    display: none;
}
.navContent{
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 500px;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
}
.navContent.active{
    background-color: #2e3136;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
}
.navContent a{
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
}

.navContent a :hover{
    background-color: #4f545c;
    border-radius: 0;

}



.menu-icon{
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
}



}