.contact {
  background: #100f0f;
  background-size: cover;
  background-position: center;
  color: #fff;
  background-attachment: fixed;
  padding-top: 70px;
}

.follow {
  background: none !important;
  padding: 10px;
  margin: 15px;
}
.contact h1 {
  text-align: center;
  color: #efefef !important;
  padding-bottom: 10px;
  padding-top: 50px;
}
.contact-info .fa {
  margin: 10px;
  color: #007bff;
  font-weight: bold;
}
.contact h1::after {
  content: "";
  background: #efefef;
  display: block;
  height: 3px;
  width: 170px;
  margin: 20px auto 5px;
}
.contact-form {
  padding: 15px;
}

.form-control {
  border-radius: 0 !important;
  border: none !important;
}

::placeholder {
  color: #999 !important;
}

.footer {
  background: black;
  color: #fff;
  padding: 12px;
}
.service-header {
  background-color:  rgb(31, 30, 30);
  box-shadow: 0 2px 20px rgb(196, 191, 191);
  
}

.connect-right{
  background-color: rgb(31, 30, 30);
  box-shadow: 0 2px 20px rgb(196, 191, 191);
  padding-top: 12px;
}
.service-header h1 {
  background-color: rgb(55, 55, 52);
}
.service-header p {
  text-align: center;
  color: white;
  font-size: 20px;
}

.connect-text{
  text-align: center;
  padding-bottom: 25px;
}