.about {
  padding-top: 100px;
  padding-bottom: 50px;
  color: #f0f0f0;
  background: #100f0f;
  background-size: cover;
  background-attachment: fixed;
  font-size: larger;
}
#about-header {
  color: #f0f0f0;
}

.toggle {
  background-color: #6288c4;
  border: 5px solid #6288c4;
  border-radius: 5px;
  margin-top: 5px;
  margin-bottom: 30px;
}

.separator {
  border-left: 2px solid rgb(157, 154, 154);
  height: 420px;
  position: absolute;
  left: 50%;
  margin-left: -3px;
 
}

  @media screen and (max-width: 960px){
      .separator{
          display: none;
      }
  }

.toggle :hover {
  background-color: lawngreen;
}

img {
  border-radius: 7px;
}
